const regex = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/,
	img = 'https://www.sweetqianba.com/group1/M00/00/00/rBsAEV0R21WAb6opAABD7xXMsJ4183.png';
import {
	appLogin,
	getInfoByToken
} from '../../api/auth.js'
import {
	putUserInfo
} from '../../setting/putUserInfo.js'
export default {
	data() {
		return {
			providerList: [],
			hasProvider: false,
			account: '',
			password: '',
			positionTop: 0
		}
	},
	methods: {
		initPosition() {
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		bindLogin() {
			if (!regex.test(this.account)) {
				uni.showToast({
					icon: 'none',
					title: '请填写正确的手机号'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			const loginData = {
				telephone: this.account,
				password: this.password
			}
			appLogin(loginData).then(data => {
				if (data.errno == 200) {
					if (data.data) {
						putUserInfo(data).then(data => {
							console.log(data)
						}).catch(err => {
							console.log(err)
						})
					}
					uni.switchTab({
						url: '../main/main',
					})
				} else {
					uni.showToast({
						icon: 'none',
						title: `登录失败，${data.errmsg}`
					});
				}
			}).catch(err => {
				uni.showToast({
					icon: 'none',
					title: `登录失败，${err}`
				});
				console.log(err)
			})
		},
		wxLogin: function() {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					console.log(res)
					uni.request({
						url: `https://api.weixin.qq.com/sns/userinfo?access_token=${res.authResult.access_token}&openid=${res.authResult.openid}`,
						success: (wechatreturn) => {
							uni.getUserInfo({
								provider: 'weixin',
								withCredentials: true,
								success: (infoRes) => {
									appLogin(Object.assign(infoRes.userInfo, {
										wechatreturn: wechatreturn.data
									})).then(res => {
										console.log(res)
										// this.login({
										// 	userinfo: res.data.userinfo,
										// 	token: res.data.token
										// })
										// uni.setStorageSync('token', res
										// 	.data.token)
										// uni.setStorageSync('userinfo',
										// 	res.data.userinfo)
										uni.navigateBack({
											delta: 1
										})
									}).catch(err => {
										console.log(JSON.stringify(err))
										uni.showToast({
											title: '登录失败',
											icon: "none"
										})
									})
								},
								fail(err) {
									console.log(JSON.stringify(err));
									uni.showToast({
										title: '登录失败',
										icon: "none"
									})
								}
							});
						}
					})

				},
				fail: (err) => {
					console.error('授权登录失败：' + JSON.stringify(err));
					uni.showToast({
						title: '登录失败',
						icon: "none"
					})
				}
			});
		}
	},
	onReady() {
		this.initPosition();
	}
}
